@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}
@media only screen and (max-width: 68.75em) {
  html {
    font-size: 50%;
  }
}

body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  line-height: 1.6;
  color: #333;
  min-height: 100vh;
  background-image: linear-gradient(to right bottom, #f36, #ba265d);
  background-size: cover;
  background-repeat: no-repeat;
}

li {
  list-style-type: none;
}

a {
  display: inline-block;
  text-decoration: none;
}

.logo {
  margin-left: 2rem;
  height: 3.25rem;
}

.search {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex: 0 0 40%;
}
.search__button {
  border: none;
  background-color: #f4f2f2;
  cursor: pointer;
}
.search__button:focus {
  outline: none;
}
.search__button:active {
  transform: translateY(2px);
}
.search__input {
  margin-right: -3.25rem;
  border: none;
  border-radius: 100px;
  padding: 0.7rem 2rem;
  width: 90%;
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: #f4f2f2;
  transition: all 0.3s ease;
}
.search__input::placeholder {
  font-weight: 100;
  color: #ccc;
}
.search__input:focus {
  outline: none;
  width: 100%;
  background-color: #f0eeee;
}
.search__icon {
  width: 2rem;
  height: 2rem;
  fill: #999;
}
.search__input:focus + .search__button {
  background-color: #f0eeee;
}
@media only screen and (max-width: 28.125em) {
  .search {
    flex: 0 0 100%;
    order: 1;
    background-color: #f4f2f2;
  }
}

.user-nav {
  display: flex;
  align-items: center;
  align-self: stretch;
}
.user-nav > * {
  padding: 0 2rem;
  height: 100%;
  transition: all 0.3s ease;
  cursor: pointer;
}
.user-nav > *:hover {
  background-color: #f4f2f2;
}
.user-nav__icon-box {
  display: flex;
  position: relative;
  align-items: center;
}
.user-nav__icon {
  width: 2.25rem;
  height: 2.25rem;
  fill: #777;
}
.user-nav__notification {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 1rem;
  top: 1.5rem;
  border-radius: 100px;
  width: 1.75rem;
  height: 1.75rem;
  font-size: 1.2rem;
  color: #fff;
  background-color: #eb2f64;
}
.user-nav__user {
  display: flex;
  align-items: center;
}
.user-nav__user-photo {
  margin-right: 1rem;
  border-radius: 100px;
  height: 3.75rem;
}

.side-nav {
  margin-top: 3.5rem;
  font-size: 1.4rem;
}
.side-nav__item {
  position: relative;
}
.side-nav__item:not(:last-child) {
  margin-bottom: 0.5rem;
}
@media only screen and (max-width: 56.25em) {
  .side-nav__item {
    flex: 1;
  }
  .side-nav__item:not(:last-child) {
    margin-bottom: 0;
  }
}
.side-nav__item::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  width: 3px;
  height: 100%;
  background-color: #eb2f64;
  transform: scaleY(0);
  transition: transform 0.2s, width 0.4s 0.2s cubic-bezier(1, 0, 0, 1), background-color 0.1s;
}
.side-nav__item:hover::before, .side-nav__item--selected::before {
  width: 100%;
  transform: scaleY(1);
}
.side-nav__item:active::before {
  background-color: #f36;
}
.side-nav__link:link, .side-nav__link:visited {
  display: flex;
  position: relative;
  z-index: 10;
  align-items: center;
  padding: 1.5rem 3rem;
  text-transform: uppercase;
  color: #faf9f9;
}
@media only screen and (max-width: 56.25em) {
  .side-nav__link:link, .side-nav__link:visited {
    justify-content: center;
    padding: 2rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .side-nav__link:link, .side-nav__link:visited {
    flex-direction: column;
    padding: 1.5rem;
  }
}
.side-nav__icon {
  margin-right: 2rem;
  width: 1.75rem;
  height: 1.75rem;
  fill: currentColor;
}
@media only screen and (max-width: 37.5em) {
  .side-nav__icon {
    margin-right: 0;
    margin-bottom: 0.7rem;
    width: 1.5rem;
    height: 1.5rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .side-nav {
    display: flex;
    margin: 0;
  }
}

.legal {
  padding: 2.5rem;
  font-size: 1.2rem;
  text-align: center;
  color: #ccc;
}
@media only screen and (max-width: 56.25em) {
  .legal {
    display: none;
  }
}

.gallery {
  display: flex;
}
.gallery__photo {
  display: block;
  width: 100%;
}

.overview {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f4f2f2;
}
.overview__heading {
  padding: 1.5rem 3rem;
  font-weight: 300;
  font-size: 2.2rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@media only screen and (max-width: 37.5em) {
  .overview__heading {
    padding: 1.25rem 2rem;
    font-size: 1.8rem;
  }
}
@media only screen and (max-width: 28.125em) {
  .overview__heading {
    padding: 0 1.5rem;
  }
}
.overview__stars {
  display: flex;
  margin-right: auto;
}
@media only screen and (max-width: 28.125em) {
  .overview__stars {
    margin-right: 0;
  }
}
.overview__icon-star, .overview__icon-location {
  width: 1.75rem;
  height: 1.75rem;
  fill: #eb2f64;
}
.overview__location {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
}
@media only screen and (max-width: 28.125em) {
  .overview__location {
    margin: 1rem 0;
  }
}
.overview__icon-location {
  margin-right: 1.5rem;
}
@media only screen and (max-width: 28.125em) {
  .overview__icon-location {
    margin-right: 0;
  }
}
.overview__rating {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-self: stretch;
  margin-left: 3rem;
  padding: 0 2.25rem;
  color: #fff;
  background-color: #eb2f64;
}
@media only screen and (max-width: 37.5em) {
  .overview__rating {
    padding: 0 1.5rem;
  }
}
@media only screen and (max-width: 28.125em) {
  .overview__rating {
    margin-left: 0;
  }
}
.overview__rating-average {
  margin-bottom: -3px;
  font-weight: 300;
  font-size: 2.2rem;
}
@media only screen and (max-width: 37.5em) {
  .overview__rating-average {
    font-size: 1.8rem;
  }
}
.overview__rating-count {
  font-size: 1.2rem;
  text-transform: uppercase;
}
@media only screen and (max-width: 37.5em) {
  .overview__rating-count {
    font-size: 0.5rem;
  }
}
@media only screen and (max-width: 28.125em) {
  .overview {
    flex-direction: column;
    padding: 0 3rem;
  }
}

.btn-inline {
  display: inline-block;
  border: none;
  border-bottom: 1px solid currentColor;
  padding-bottom: 2px;
  font-size: inherit;
  color: #eb2f64;
  background-color: transparent;
  transition: all 0.3s ease;
  cursor: pointer;
}
.btn-inline span {
  margin-left: 3px;
  transition: margin-left 0.2s;
}
.btn-inline:focus {
  outline: none;
  animation: pulsate 1s infinite;
}
.btn-inline:hover {
  color: #333;
}
.btn-inline:hover span {
  margin-left: 0.8rem;
}

@keyframes pulsate {
  0% {
    box-shadow: none;
    transform: scale(1);
  }
  50% {
    box-shadow: 0 1rem 4rem rgba(0, 0, 0, 0.25);
    transform: scale(1.05);
  }
  100% {
    box-shadow: none;
    transform: scale(1);
  }
}
.paragraph:not(:last-of-type) {
  margin-bottom: 2rem;
}

.list {
  display: flex;
  flex-wrap: wrap;
  margin: 3rem 0;
  border-top: 1px solid #f4f2f2;
  border-bottom: 1px solid #f4f2f2;
  padding: 3rem;
}
.list__item {
  flex: 0 0 50%;
  margin-bottom: 0.7rem;
}
.list__item::before {
  content: "";
  display: inline-block;
  margin-right: 0.7rem;
  width: 1rem;
  height: 1rem;
  background-image: url("../img/chevron-thin-right.svg");
  background-size: cover;
  background-color: #eb2f64;
}
@supports (mask-image: url()) {
  .list__item::before {
    mask-image: url("../img/chevron-thin-right.svg");
    mask-size: cover;
    background-image: none;
  }
}
@media only screen and (max-width: 28.125em) {
  .list {
    padding: 0;
  }
}

.recommend {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.3rem;
  color: #999;
}
.recommend__friends {
  display: flex;
}
.recommend__photo {
  border: 3px solid #fff;
  border-radius: 50%;
  width: 4rem;
  height: 4rem;
  box-sizing: content-box;
}
.recommend__photo:not(:last-child) {
  margin-right: -2rem;
}

.review {
  position: relative;
  overflow: hidden;
  margin-bottom: 3.5rem;
  padding: 3rem;
  box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.06);
  background-color: #fff;
}
.review::before {
  content: "“";
  position: absolute;
  left: -1rem;
  top: -2.75rem;
  z-index: 1;
  font-family: sans-serif;
  font-size: 20rem;
  line-height: 1;
  color: #f4f2f2;
}
.review__text {
  position: relative;
  z-index: 10;
  margin-bottom: 2rem;
  text-align: left;
}
.review__user {
  display: flex;
  align-items: center;
}
.review__photo {
  margin-right: 1.5rem;
  border-radius: 50%;
  width: 4.5rem;
  height: 4.5rem;
}
.review__user-box {
  margin-right: auto;
}
.review__user-name {
  margin-bottom: 0.4rem;
  font-weight: 600;
  font-size: 1.2rem;
  text-transform: uppercase;
}
.review__user-date {
  font-size: 1.2rem;
  color: #999;
}
.review__rating {
  font-weight: 300;
  font-size: 2.2rem;
  color: #eb2f64;
}
@media only screen and (max-width: 56.25em) {
  .review {
    margin-bottom: 3rem;
    padding: 2rem;
  }
}

.cta {
  padding: 3.5rem 0;
  text-align: center;
}
.cta__book-now {
  margin-bottom: 2.5rem;
  font-weight: 300;
  font-size: 2.2rem;
  text-transform: uppercase;
}
@media only screen and (max-width: 28.125em) {
  .cta__book-now {
    font-size: 1.8rem;
  }
}
@media only screen and (max-width: 56.25em) {
  .cta {
    padding: 2.5rem 0;
  }
}
@media only screen and (max-width: 28.125em) {
  .cta {
    padding: 2.5rem 3rem;
  }
}

.btn {
  position: relative;
  overflow: hidden;
  border: none;
  border-radius: 100px;
  font-weight: 300;
  font-size: 1.4rem;
  text-transform: uppercase;
  color: #fff;
  background-image: linear-gradient(to right, #f36, #ba265d);
  cursor: pointer;
}
.btn > * {
  display: inline-block;
  width: 100%;
  height: 100%;
  transition: all 0.3s ease;
}
.btn__visible {
  display: inline-block;
  padding: 2rem 7.5rem;
}
.btn__invisible {
  position: absolute;
  left: 0;
  top: -100%;
  padding: 2rem 0;
}
.btn:focus {
  outline: none;
  animation: pulsate 1s infinite;
}
.btn:hover {
  background-image: linear-gradient(to left, #f36, #ba265d);
}
.btn:hover .btn__visible {
  transform: translateY(100%);
}
.btn:hover .btn__invisible {
  top: 0;
}

.container {
  margin: 8rem auto;
  min-height: 50rem;
  max-width: 120rem;
  box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  background-color: #faf9f9;
}
@media only screen and (max-width: 75em) {
  .container {
    margin: 0;
    max-width: 100%;
  }
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f4f2f2;
  height: 7rem;
  font-size: 1.4rem;
  background-color: #fff;
}
@media only screen and (max-width: 28.125em) {
  .header {
    flex-wrap: wrap;
    align-content: space-around;
    height: 11rem;
  }
}

.content {
  display: flex;
}
@media only screen and (max-width: 56.25em) {
  .content {
    flex-direction: column;
  }
}

.sidebar {
  display: flex;
  flex: 0 0 18%;
  flex-direction: column;
  justify-content: space-between;
  background-color: #333;
}

.hotel-view {
  flex: 1;
}

.details {
  display: flex;
  border-bottom: 1px solid #f4f2f2;
  padding: 4.5rem;
  font-size: 1.4rem;
  background-color: #faf9f9;
}
@media only screen and (max-width: 56.25em) {
  .details {
    padding: 3rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .details {
    flex-direction: column;
  }
}

.description {
  flex: 0 0 60%;
  margin-right: 4.5rem;
  padding: 3rem;
  box-shadow: 0 1rem 5rem rgba(0, 0, 0, 0.06);
  font-size: 1.4rem;
  background-color: #fff;
}
@media only screen and (max-width: 56.25em) {
  .description {
    margin-right: 3rem;
    padding: 2rem;
  }
}
@media only screen and (max-width: 37.5em) {
  .description {
    margin-right: 0;
    margin-bottom: 3rem;
  }
}

.user-reviews {
  flex: 1;
  text-align: center;
}